.aside {
	min-width: 200px;
	min-height: 100vh;
	margin-right: 20px;
	padding: 20px 10px;
	background-color: #000000;
	color: #ced4da;
}

.title {
	margin-bottom: 10px;
	padding-left: 10px;
	font-size: 20px;

	&::after {
		content: '';
		display: block;
		height: 1px;
		margin-top: 10px;
		margin-left: -10px;
		background-color: #727272;
	}
}

.currentContainer::after {
	content: '';
	display: block;
	height: 1px;
	margin-top: 10px;
	margin-bottom: 10px;
	background-color: #727272;
}

.subtitle {
	margin-bottom: 5px;
	padding-left: 10px;
	font-size: 14px;
	font-weight: 400;
}

.repoWrapper {
	padding: 5px 10px;
	border-radius: 10px;
	background-color: #ced4da;
	color: #727272;
	background-color: #727272;
	color: #ececec;
	font-size: 12px;
	font-weight: 300;
	transition: background-color 250ms;

	li > &:hover {
		background-color: #6495ed;
	}
}

.repoHeader {
	color: #ececec;
	font-weight: 500;
}

.button {
	display: block;
	margin-top: 30px;
	margin-inline: auto;
}

.repoItem {
	cursor: pointer;

	&:not(:last-child) {
		margin-bottom: 5px;
	}
}
